import React from "react";
import {
  Row,
  Col,
  Typography,
  Table,
  Button,
  Switch,
  Popconfirm,
  Divider
} from "antd";
import { appAccountModel as Accounts } from "../../../shared";
import AddAccount from "./AddAccount";

class AppAccounts extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      accounts: [],
      isModalOpen: false
    };
  }

  openModal = () => this.setState({ isModalOpen: true });
  closeModal = () => this.setState({ isModalOpen: false });

  removeAccountHandler = id => () => {
    Accounts.removeAccount(id);
    this.setState({
      accounts: Accounts.getAccounts(this.props.app.appName)
    });
  };

  updateAccountTable = () =>
    this.setState({
      accounts: Accounts.getAccounts(this.props.app.appName)
    });

  accountSuspendingHandler = id => value => {
    Accounts.suspendAccount(id, value);
    this.setState({
      accounts: Accounts.getAccounts(this.props.app.appName)
    });
  };

  componentDidMount() {
    this.setState({
      accounts: Accounts.getAccounts(this.props.app.appName)
    });
  }

  tableColumns = [
    {
      key: "name",
      title: "Account Name",
      dataIndex: "name"
    },
    {
      key: "role",
      title: "Role",
      dataIndex: "role"
    },
    {
      key: "password",
      title: "Password",
      dataIndex: "password"
    },
    {
      key: "remove",
      title: "Remove Account",
      render: record => (
        <Popconfirm
          key={record.id}
          okText="Delete"
          onConfirm={this.removeAccountHandler(record.id)}
          title={"Are you sure your want to remove " + record.name}
        >
          <Button type="danger" id={record.id} icon="delete" size="small">
            Remove
          </Button>
        </Popconfirm>
      )
    },
    {
      key: "suspend",
      title: "Suspend",
      render: record => (
        <Switch
          key={record.id}
          onChange={this.accountSuspendingHandler(record.id)}
          checked={record.isSuspended}
        />
      )
    }
  ];

  render() {
    const selectedTheme = this.props.selectedTheme;
    return (
      <div>
        <Row>
          <Col span={24}>
            <Typography.Title
              style={{
                color: selectedTheme === "dark" ? "whitesmoke" : ""
              }}
            >
              App Accounts
            </Typography.Title>
          </Col>
          <Col>
            <Divider />
          </Col>
        </Row>
        <Row type="flex" justify="end">
          <Col className="bottomMargin">
            <Button
              onClick={this.openModal}
              shape="circle"
              size="large"
              type="primary"
              icon="plus"
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              scroll={{ x: true }}
              rowKey="id"
              columns={this.tableColumns}
              dataSource={this.state.accounts}
              pagination={false}
            />
          </Col>
        </Row>
        <AddAccount
          addAccount={Accounts.addAccount}
          closeModal={this.closeModal}
          isModalOpen={this.state.isModalOpen}
          appName={this.props.app.appName}
          updateAccountTable= {this.updateAccountTable}
        />
      </div>
    );
  }
}
export default AppAccounts;
